import React from "react";
const Subcard =(props)=>{
return(
    <>
   <div class="card mb-4 rounded-3 shadow-sm col-4 m-2" >
      <div class="face face1">
          <h1>
              {props.title}
          </h1>
          <p>{props.exp}</p>
     
       
      </div>
     
      <div class="face face2">
      <i class={props.icon}></i>
      
      </div>
  </div>


    </>
)
}
export default Subcard;