const Pricing =()=>{
    return(
        <>
           <div className="heading-top text-center ">
               <h1>CREATOR BIZ SERVICES</h1>
               <p>We offer in which We're Pro</p>
           </div>
        <div className="container-fluid d-flex justify-content-center flex-wrap ">
     {
         PricingApi.map((e)=>{
            return(
                <>
                    <PricingDiv />
                </>
            ) 
         })
     }       

       
      
        </div>
        </>
    )
}
export default Pricing;