import React from "react";
import Subcard from "./Subcard";
import Link from "./Links";
const Cards =()=>{
    return(
        <>
        <div className="heading-top text-center ">
               <h1>CREATOR BIZ SERVICES</h1>
               <p>We offer in which We're Pro</p>
           </div>
        <div className="container-fluid main col-12 ">
              {/* //////////subcard */}
              <div className="container-fluid col-12 d-flex flex-wrap justify-content-center m-2">
                  {
                      Link.map((val)=>{
                        return(
                            <>
                               <Subcard 
                        key={val.id}
                        icon={val.icon}
                        title={val.title}
                        exp={val.exp}
                        />
                            </>
                        )
                     
                      })
                  }

      </div>
    {/* ////////////subcard */}
        </div>
        </>
    )
}
export default Cards;